import React, {Fragment} from 'react';
import {connect} from "react-redux";

import MessageComponent from "../../Chat/Chat";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../../Images/IconPack";
import { FormattedMessage } from 'react-intl';
import { useHistory } from "react-router-dom";


const MessageTable = (props) => {
    let history = useHistory();

    const rowHandler = (data) => {
        history.push('tickets/'+data.chatId);
    }

    const messageComponent = (rowData) => {
        return (<Fragment><span className="message-author">{rowData.data[rowData.data.length-1].author}:</span>{rowData.data[rowData.data.length-1].message}</Fragment>);
    };
    const lastMessageDate = (rowData) => {
        return (<Fragment>{rowData.data[rowData.data.length-1].date}</Fragment>);
    };

    const tableActionHandler = (data) => {
        // open popup
        // insert data
    }


    let rows =  props.tickets.map((item) => {
        return (<TableRow onClick={()=> {rowHandler(item)}}>
            <TableCell className="padding-sides-20 color-white">{item.title}</TableCell>
            <TableCell className="padding-sides-10 color-white">{messageComponent(item)}</TableCell>
            <TableCell className="padding-sides-20 text-align-right">{item.creationDate}</TableCell>
            <TableCell className="padding-sides-25 text-align-right">{lastMessageDate(item)}</TableCell>
        </TableRow>)
    });




    /* active */
    return (
        <div className="сustomStyle">
            <Table className="dealsTable">
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-20 w175"><FormattedMessage  id='table.title' /></TableCell>
                        <TableCell className="padding-sides-10"><FormattedMessage  id='table.lastMessage' /></TableCell>
                        <TableCell className="padding-sides-25 w150 text-align-right"><FormattedMessage  id='table.creationDate' /></TableCell>
                        <TableCell className="padding-sides-25 w150 "><FormattedMessage  id='table.lastMessage' /></TableCell>

                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows}
                </TableBody>
            </Table>
        </div>
    );
}
const mapStateToProps = (state) => ({
    tickets: state.tickets.tickets,
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MessageTable);