import React from 'react';
import {IconPack} from './../../../Images/IconPack';
import './style.scss';
import Slider from '@material-ui/core/Slider';
import {setBuyCLicenseModal, setBuyPLicenseModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";
import TextField from "@material-ui/core/TextField/TextField";
import Button from "@material-ui/core/Button";
import { FormattedMessage } from 'react-intl';

const LicensesNew = (props) => {

    const [value1, setValue1] = React.useState(1000);
    const [card1Component, setCard1Component] = React.useState(false);
    const [value2, setValue2] = React.useState(5000);
    const [card2Component, setCard2Component] = React.useState(false);

    const [card1Validation, setCard1Validation] = React.useState(true);
    const [card2Validation, setCard2Validation] = React.useState(true);

    let personalLicenseData = {
        dayCost: (8 - value1/100*0.1277).toFixed(2),
        daysAtAll: (value1 / (8 - value1/100*0.1277).toFixed(2) ).toFixed(0),
        configCost: (8 - value1/100*0.0797).toFixed(2),
        configAtAll: (value1 / (10 - value1/100*0.0797).toFixed(2)).toFixed(0),
    }

    let corporateLicenseData = {
        dayCost: (8 - value2/5000*0.4312).toFixed(2),
        daysAtAll: (value2 / (8 - value2/5000*0.4312).toFixed(2) ).toFixed(0),
        configCost: (8 - value2/5000*0.2797).toFixed(2),
        configAtAll: (value2 / (10 - value2/5000*0.2797).toFixed(2)).toFixed(0),
    }

    const countDigits = (n) => {
        for(var i = 0; n > 1; i++) {
            n /= 10;
        }
        return i;
    }

    const handleChange1 = (event, newValue) => {
        validation(newValue, 1)
        setValue1(newValue);
    };
    const handleChange11 = (event) => {
        validation(event.target.value, 1)
        setValue1(event.target.value);
    };
    const handleChange2 = (event, newValue) => {
        validation(newValue, 2)
        setValue2(newValue);
    };
    const handleChange22 = (event) => {
        validation(event.target.value, 2)
        setValue2(event.target.value);
    };

    const validation = (val, card) => {

        if(card === 1) {
            if(val < 100 || val > 5000) {
                setCard1Validation(false);
            }else {
                setCard1Validation(true);
            }

        }else if(card === 2) {
            if(val < 5000 || val > 50000) {
                setCard2Validation(false);
            }else {
                setCard2Validation(true);
            }
        }
    }

    let licenseCardComponent = <div className="value1"><span className="doll">$</span>{value1}<div className="change" onClick={()=>{setCard1Component(true)}}>{IconPack.buttons.change}</div></div>;
    if(card1Component) {
        licenseCardComponent = <div className="value2">
            <TextField
                label="Amount to replenish"
                fullWidth
                rows={1}
                value={value1}
                onChange={(e)=>{handleChange11(e)}}
                variant="filled"
                type="number"
                className={card1Validation ? "inputDollar" : "inputDollar error"}
                min="100" max="5000"
            />
            <div className="change" onClick={()=>{setCard1Component(false)}}>{IconPack.buttons.change}</div>
        </div>
    }


    let licenseCardComponent2 = <div className="value1"><span className="doll">$</span>{value2}<div className="change" onClick={()=>{setCard2Component(true)}}>{IconPack.buttons.change}</div></div>;
    if(card2Component) {
        licenseCardComponent2 = <div className="value2">
            <TextField
                label="Amount to replenish"
                fullWidth
                rows={1}
                value={value2}
                onChange={(e)=>{handleChange22(e)}}
                variant="filled"
                type="number"
                className={card2Validation ? "inputDollar" : "inputDollar error"}
                min="5000" max="50000"
            />
            <div className="change" onClick={()=>{setCard2Component(false)}}>{IconPack.buttons.change}</div>
        </div>
    }

    let card1ValidationMessage;
    if(value1 < 100) {
        card1ValidationMessage = <span className='validation-message'>Minimum amount: <span>100$</span></span>
    }else if(value1 > 5000) {
        card1ValidationMessage = <span className='validation-message'>Maximum amount: <span>5000$</span></span>
    }

    let card2ValidationMessage;
    if(value2 < 5000) {
        card2ValidationMessage = <span className='validation-message'>Minimum amount: <span>5000$</span></span>
    }else if(value2 > 50000) {
        card2ValidationMessage = <span className='validation-message'>Maximum amount: <span>50000$</span></span>
    }
    return (
        <div className="LicensesNew--dashboard">
            <div className="content">
                <header>
                    <h1 className="center">
                        <FormattedMessage id='licenseStarter.title'/>
                        </h1>
                    <h3 className="center">
                        <FormattedMessage id='licenseStarter.subTitle'/>
                        </h3>
                    <p className="center">
                        <FormattedMessage id='licenseStarter.description'/>
                        </p>
                </header>

                <main>
                    <div className="row">
                        <div className="license_card license_card--width ">
                            <div className="header">
                                <div className="line blue"></div>
                                <h4 className="center">
                                    <FormattedMessage id='licenseStarter.card1.title'/></h4>
                                <h6 className="center">
                                    <FormattedMessage id='licenseStarter.card1.subTitle'/></h6>

                                <div className="option">

                                    {licenseCardComponent}

                                    <div className="range">
                                        <Slider
                                            step={100}
                                            min={100}
                                            max={5000}
                                            value={value1} onChange={handleChange1}  />
                                    </div>
                                </div>
                                {card1Validation ? (<div className="secondOption">
                                    <div>
                                        <div><span className="white">${personalLicenseData.dayCost}</span>/ <FormattedMessage id='licenseStarter.day'/></div>
                                        <div><span className="white">${personalLicenseData.configCost}</span>/ <FormattedMessage id='licenseStarter.config'/></div>
                                    </div>
                                    <div className="center">{personalLicenseData.daysAtAll} <FormattedMessage id='licenseStarter.days'/> <FormattedMessage id='licenseStarter.or'/> {personalLicenseData.configAtAll} <FormattedMessage id='licenseStarter.configs'/></div>
                                </div> ) :  (<div className="secondOption">
                                    {card1ValidationMessage}
                                </div>)}

                            </div>
                            <div className="footer">
                                <Button variant="contained"
                                        className={card1Validation ? "btn btn--blue" : "btn btn--blue btn--blue--dissabled"}
                                        onClick={()=>{
                                            if(card2Validation) {
                                                props.setBuyPLicenseModal({bool: true, data: value1})
                                            }
                                        }}>
                                    <FormattedMessage id='licenseStarter.button'/>
                                </Button>
                            </div>
                        </div>




                        <div className="vs">
                            vs
                        </div>
                        <div className="license_card license_card--width ">
                            <div className="header">
                                <div className="line pink"></div>
                                <h4 className="center"><FormattedMessage id='licenseStarter.card2.title'/></h4>
                                <h6 className="center"><FormattedMessage id='licenseStarter.card2.subTitle'/></h6>
                                <div className="option">
                                    {licenseCardComponent2}
                                    <div className="range">
                                        <Slider
                                            step={100}
                                            min={5000}
                                            max={50000}
                                            value={value2} onChange={handleChange2} aria-labelledby="continuous-slider" />
                                    </div>
                                </div>

                                {card2Validation ? (<div className="secondOption">
                                    <div>
                                        <div><span className="white">${corporateLicenseData.dayCost} </span> / <FormattedMessage id='licenseStarter.day'/></div>
                                        <div><span className="white">${corporateLicenseData.configCost} </span> / <FormattedMessage id='licenseStarter.config'/></div>
                                    </div>
                                    <div className="center">
                                        {corporateLicenseData.daysAtAll} <FormattedMessage id='licenseStarter.days'/> <FormattedMessage id='licenseStarter.or'/> {corporateLicenseData.configAtAll} <FormattedMessage id='licenseStarter.configs'/></div>
                                </div>) : (<div className="secondOption">
                                    {card2ValidationMessage}
                                </div>)}





                            </div>
                            <div className="footer">
                                <Button variant="contained"
                                        className={card2Validation ? "btn btn--blue" : "btn btn--blue btn--blue--dissabled"}
                                        onClick={()=>{
                                            if(card2Validation){
                                                props.setBuyCLicenseModal({bool: true, data: value2 })
                                            }
                                        }}>
                                    <FormattedMessage id='licenseStarter.button'/>
                                </Button>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="license_card--width">
                            <FormattedMessage id='licenseStarter.card1.description'/>
                        </div>
                        <div>

                        </div>
                        <div className="license_card--width">
                            <FormattedMessage id='licenseStarter.card2.description'/>
                        </div>
                    </div>

                    <div className="grey-line"></div>
                </main>

                <footer>

                    <nav>
                        <ul>
                            <li><FormattedMessage id='licenseStarter.menu.aboutLicense'/></li>
                            <li><FormattedMessage id='licenseStarter.menu.termsOfUse'/></li>
                            <li><FormattedMessage id='licenseStarter.menu.refundPolicy'/></li>
                            <li><FormattedMessage id='licenseStarter.menu.faqSection'/></li>
                        </ul>
                    </nav>
                </footer>

            </div>
        </div>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setBuyCLicenseModal: (obj) => dispatch(setBuyCLicenseModal(obj)),
        setBuyPLicenseModal: (obj) => dispatch(setBuyPLicenseModal(obj))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LicensesNew);