import React, {Fragment} from 'react';
import {connect} from "react-redux";
import {BrowserRouter as Router,
    useHistory
} from "react-router-dom";

const Documentation = (props) => {
    let history = useHistory();



    return (
        <Fragment>
            <div className="dashboard-content">
                <span onClick={   history.push('auth/sign_in')}>Sign IN</span>
                <span onClick={   history.push('auth/sign_up')}>Sign up</span>
            </div>
        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Documentation);