import React from 'react';
import './style.scss'
import {setChatId, setChatState} from "../../../redux/app/app.actions";
import {createTicket} from "../../../redux/support/tickets/tickets.actions";
import {connect} from "react-redux";
import { IconPack } from "../../../Images/IconPack";

const Installer = (props) => {

    let [cardsCount, setCardsCount] = React.useState(0);

    let releasesItems = props.releases.map((item, key) => {

        if(cardsCount >= key) {

            let changelog = item.changelog.map((item, key) => {
                return (<li key={key}>{IconPack.installerIcons.complete}{item}</li>)
            });
            let minRequirements = item.minRequirements.map((item, key) => {
                return (<li key={key}>{IconPack.installerIcons.warning}{item}</li>)
            });


            const btnHandler = (numb) => {

            }



            return (
                <div className="dashboard-installer__card" key={key}>
                    <header>
                        <h2>{item.title}<span>{item.secondTitle}</span></h2>
                        <span>Release date:  <span className="white">{item.release}</span></span>

                        <div className="btns-row">
                            <div className="btn btn--blue" onClick={()=>{btnHandler()}}>FOR WINDOWS</div>
                            <div className="btn" onClick={()=>{btnHandler()}}>FOR MACOS</div>
                            <div className="btn" onClick={()=>{btnHandler()}}>FOR LINUX</div>
                        </div>

                    </header>

                    <main>
                        <div className="option">
                            <h3>Minimum requirements</h3>
                            <div className="grey-line"></div>
                            <ul>{minRequirements}</ul>
                        </div>

                        <div className="option">
                            <h3>Supported versions for system installation</h3>
                            <div className="grey-line"></div>
                            <ul>
                                <li>{IconPack.installerIcons.complete}Windows: <span>{item.supportedVersions.windows}</span></li>
                                <li>{IconPack.installerIcons.complete}MacOs: <span>{item.supportedVersions.macOs}</span></li>
                                <li>{IconPack.installerIcons.complete}Linux: <span>{item.supportedVersions.linux}</span></li>
                            </ul>
                        </div>

                        <div className="option">
                            <h3>Changelog</h3>
                            <div className="grey-line"></div>
                            <ul>{changelog}</ul>
                        </div>
                    </main >

                </div>
            );
        }
    });


    window.requestAnimationFrame(function() {
        let chatWrapper = document.querySelector('.releasesWrapper');
        if (chatWrapper !== undefined) {
            chatWrapper.scrollTop = chatWrapper.scrollHeight;
        }
    });

    const moreHandler = () => {
        setCardsCount(cardsCount+1);

    };


    return (
        <div className="dashboard-installer">
            <div className="releasesWrapper">
                {releasesItems}
                <div className="btn-more" onClick={()=>{moreHandler()}}>Past version</div>
            </div>
        </div>
    );
};


const mapStateToProps = (state) => ({
    releases: state.releases.releases,
});



export default connect(mapStateToProps)(Installer);