import React, {Fragment} from 'react';
import {connect} from "react-redux";

import {getDate} from "../../../../redux/Utils";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../../Images/IconPack";
import { FormattedMessage } from 'react-intl';


import {setLicenseDaysSettingsModal, setChangeDealModal} from "../../../../redux/app/app.actions";

const DealsTable = (props) => {

    const tableAction = (rowData) => {
        return (<div className="tableAction" onClick={()=>{tableActionHandler(rowData)}}><svg width="5" height="14" viewBox="0 0 5 14" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.5 5.29688C1.48438 5.29688 0.671875 6.13477 0.671875 7.125C0.671875 8.14062 1.48438 8.95312 2.5 8.95312C3.49023 8.95312 4.32812 8.14062 4.32812 7.125C4.32812 6.13477 3.49023 5.29688 2.5 5.29688ZM0.671875 2.65625C0.671875 3.67188 1.48438 4.48438 2.5 4.48438C3.49023 4.48438 4.32812 3.67188 4.32812 2.65625C4.32812 1.66602 3.49023 0.828125 2.5 0.828125C1.48438 0.828125 0.671875 1.66602 0.671875 2.65625ZM0.671875 11.5938C0.671875 12.6094 1.48438 13.4219 2.5 13.4219C3.49023 13.4219 4.32812 12.6094 4.32812 11.5938C4.32812 10.6035 3.49023 9.76562 2.5 9.76562C1.48438 9.76562 0.671875 10.6035 0.671875 11.5938Z" fill="white" fillOpacity="0.6"/>
        </svg></div>);
    };

    const tableActionHandler = (data) => {
        props.setChangeDealModal({bool:true, data:data})
    }

    const [searchValue, setSearchValue] = React.useState('');
    const [dateReverse, setDateReverse] = React.useState(false);


    const getTbodyData = (reversed) => {

        if(!reversed){
            return props.deals.map((i, key) => {
                    return  getRowItem(i);
                });
        }else {
            return props.deals.map((i, key) => {
                    return  getRowItem(i);
                }).reverse()
        }
    };

    let getRowItem = (item) => {
        return (<TableRow onClick={()=> {tableActionHandler(item)}}>
            <TableCell className="padding-sides-20  color-white">{item.title}</TableCell>
            <TableCell className="padding-sides-5 color-white">{item.status}</TableCell>
            <TableCell className="padding-sides-10"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}{item.seller}</span></TableCell>
            <TableCell className="padding-sides-20"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}{item.buyer}</span></TableCell>
            <TableCell className="padding-sides-15 color-white text-align-right">{item.amount}</TableCell>
            <TableCell className="padding-sides-25 color-white">{item.dealId}</TableCell>
            <TableCell className="padding-sides-0 ">{item.dateCreated} {tableAction(item)}</TableCell>
        </TableRow>)
    };





    /* active */
    return (
        <div className="сustomStyle">
            <Table className="dealsTable">
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-20 "><FormattedMessage  id='table.dealTitle' /></TableCell>
                        <TableCell className="padding-sides-5 "><FormattedMessage  id='table.status' /></TableCell>
                        <TableCell className="padding-sides-10 w150"><FormattedMessage  id='table.seller' /></TableCell>
                        <TableCell className="padding-sides-20 w150"><FormattedMessage  id='table.buyer' /></TableCell>
                        <TableCell className="padding-sides-15 w80 text-align-right"><FormattedMessage  id='table.amount' /></TableCell>
                        <TableCell className="padding-sides-25 w150"><FormattedMessage  id='table.dealID' /></TableCell>
                        <TableCell className={dateReverse ? 'padding-sides-0 w150 td-date' : 'padding-sides-0 w150 td-date rotate-child-180 '} onClick={()=>{setDateReverse(!dateReverse)}}>{IconPack.tableIcons.date} <FormattedMessage  id='table.dateCreated' /></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {getTbodyData(dateReverse)}
                </TableBody>
            </Table>
        </div>
    );
}
const mapStateToProps = (state) => ({
    deals: state.deals.deals,
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setLicenseDaysSettingsModal: (type) => dispatch(setLicenseDaysSettingsModal(type)),
        setChangeDealModal: (type) => dispatch(setChangeDealModal(type)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DealsTable);