import React, { useState, useEffect } from 'react';
import MaterialTable from 'material-table';
import ConfigLimits from "../../TableComponents/ConfigLimits/ConfigLimits";
import DaysLeft from "../../TableComponents/DaysLeft/DaysLeft";
import {connect} from "react-redux";
import Switch from '@material-ui/core/Switch';

import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { setDeviceFilter, setOsFilter, setOsVersionFilter, setBrowserFilter, setBrowserVersionFilter, updateData, selectItem, searchData } from "../../../redux/config-shop/config.actions";
import TextField from "@material-ui/core/TextField/TextField";
import { IconPack } from "../../../Images/IconPack";
import Checkbox from '@material-ui/core/Checkbox';
import { FormattedMessage } from 'react-intl';


const Purchased = (props) => {

    useEffect(() => {
        props.setDeviceFilter('Desktop');
        props.setOsFilter('Windows');
        props.updateData();
    }, []);
    const [searchValue, setSearch] = React.useState('');

    const [isSearchActive, setSearchActive] = React.useState(false);
    const [isCheckboxesActive, setCheckboxesActive] = React.useState(true);

    const searchEventHandler = (e) => {

        if(e.target.value != 0) {
            setSearch(e.target.value);
            props.searchData(e.target.value);
            setSearchActive(true)
        }else {
            setSearch(e.target.value);
            setSearchActive(false)
        }


    }

    const deviceEventHandler = (e) => {
        props.setDeviceFilter(e.target.value);
        props.updateData();
    }
    const osEventHandler = (e) => {
        props.setOsFilter(e.target.value);

        let currentIndex = props.configPurchased.sort.os.find((obj)=> {
                return obj.title === e.target.value
        });
        props.setOsVersionFilter(currentIndex.version[0].toString());
        props.updateData();
    }
    const osVersionsEventHandler = (e) => {
        props.setOsVersionFilter(e.target.value);
        props.updateData();
    }

    const browserEventHandler = (e) => {
        props.setBrowserFilter(e.target.value);

        let currentIndex = props.configPurchased.sort.browser.find((obj)=> {
            return obj.title === e.target.value
        });

        props.setBrowserVersionFilter(currentIndex.version[0].toString());
        props.updateData();
    }
    const browserVersionsEventHandler = (e) => {
        props.setBrowserVersionFilter(e.target.value);
        props.updateData();
    }

    const buyEventHandler = (dataObj) => {

        setCheckboxesActive(true)
    }
    const checkBoxHandler = (data, value) => {

        props.selectItem(data);
    }



    const tableCheckBox = (row) => {
        return (<TableCell scope="row" className="table_checkbox">
                <Checkbox  color="primary"  checked={row.isSelected} /></TableCell>);
    }


    let tableSearchContent = ( props.configPurchased.searchData.map((row, key) => {
        return (<TableRow className={row.isSelected ? 'active' : null}
                          onClick={()=>{buyEventHandler(row);  checkBoxHandler(row)}}
                          key={key}>
            {isCheckboxesActive ? tableCheckBox(row) : null}
            <TableCell scope="row">
                <div className="shopObject color-white">
                    <div className="color-white">{row.deviceObject.icon}{row.deviceObject.title}</div>
                    <div className="color-white">{row.osObject.icon}{row.osObject.version}</div>
                    <div className="color-white">{row.browserObject.icon}{row.browserObject.version}</div>
                </div>
            </TableCell>
            <TableCell className="padding-sides-10 color-white">{row.screen}</TableCell>
            <TableCell className="padding-sides-5 color-white">{row.userAgent}</TableCell>
            <TableCell className="padding-sides-5 color-white">{row.videoCard}</TableCell>
            <TableCell className="padding-sides-15">{row.created}</TableCell>
            <TableCell className="padding-sides-0 icon-inside"><div className="align-items-baseline">{IconPack.userIcons.forTable} Lionel Messi</div></TableCell>
            <TableCell className="padding-sides-15 ">$<b className="color-white">{row.price}</b></TableCell>
            <TableCell><div className="btn-buy">USE</div></TableCell>
        </TableRow>);
        }));
    let tableContent = ( props.configPurchased.tableData.map((row, key) => {
                return (<TableRow className={row.isSelected ? 'active' : null}
                                  onClick={()=>{buyEventHandler(row);  checkBoxHandler(row)}}
                                  key={key}>
                    {isCheckboxesActive ? tableCheckBox(row) : null}
                        <TableCell scope="row">
                            <div className="shopObject color-white">
                                <div className="color-white">{row.deviceObject.icon}{row.deviceObject.title}</div>
                                <div className="color-white">{row.osObject.icon}{row.osObject.version}</div>
                                <div className="color-white">{row.browserObject.icon}{row.browserObject.version}</div>
                            </div>
                        </TableCell>
                        <TableCell className="padding-sides-10 color-white">{row.screen}</TableCell>
                        <TableCell className="padding-sides-5 color-white">{row.userAgent}</TableCell>
                        <TableCell className="padding-sides-5 color-white">{row.videoCard}</TableCell>
                        <TableCell className="padding-sides-25 text-align-right">{row.created}</TableCell>
                        <TableCell className="padding-sides-0 icon-inside"><div className="align-items-baseline">{IconPack.userIcons.forTable} Lionel Messi</div></TableCell>
                        <TableCell className="padding-sides-15 ">$<b className="color-white">{row.price}</b></TableCell>
                        <TableCell><div className="btn-buy">USE</div></TableCell>
                    </TableRow>);
            }));



    return (<div className="сustomStyle">
            <div className="filterPanel">
                <div>
                <FormControl variant="filled" className='w160 custom_label'>
                    <InputLabel id="demo-simple-select-filled-label"><FormattedMessage  id='filterPanel.device' /></InputLabel>
                    <Select
                        labelId="demo-simple-select-filled-label"
                        value={props.configPurchased.currentSort.device}
                        onChange={(e)=>{deviceEventHandler(e)}}>
                        {props.configPurchased.sort.device.map((i, key) => {
                            return (<MenuItem key={key} value={i.title}>{i.icon}{i.title}</MenuItem>);
                        })}
                    </Select>
                </FormControl>
                </div>
                <div className="filterPanel__doubled">
                    <FormControl variant="filled" className='w160 custom_label'>
                        <InputLabel id="demo-simple-select-filled-label"><FormattedMessage  id='filterPanel.operationSystem' /></InputLabel>
                        <Select
                            labelId="demo-simple-select-filled-label"
                            value={props.configPurchased.currentSort.os}
                            onChange={(e)=>{osEventHandler(e)}}>


                            {props.configPurchased.sort.os.map((i, key) => {

                                 return (<MenuItem key={key} value={i.title}>{i.icon}{i.title}</MenuItem>);

                            })}

                        </Select>
                    </FormControl>
                    <FormControl variant="filled" className='w118'>
                        <InputLabel id="demo-simple-select-filled-label"><FormattedMessage  id='filterPanel.version' /></InputLabel>
                        <Select
                            labelId="demo-simple-select-filled-label"
                            value={props.configPurchased.currentSort.osVersion}
                            onChange={(e)=>{osVersionsEventHandler(e)}}>

                            {props.configPurchased.sort.os.map((i) => {
                                if(i.title === props.configPurchased.currentSort.os){
                                    return i.version.map((version, key) => {
                                        return (<MenuItem key={key} value={version}>{version}</MenuItem>);
                                    });
                                }
                            })}

                        </Select>
                    </FormControl>
                </div>

                <div className="filterPanel__doubled">
                    <FormControl variant="filled" className='w160 custom_label'>
                        <InputLabel id="demo-simple-select-filled-label"><FormattedMessage  id='filterPanel.browser' /></InputLabel>
                        <Select
                            labelId="demo-simple-select-filled-label"
                            value={props.configPurchased.currentSort.browser}
                            onChange={(e)=>{browserEventHandler(e)}}>

                            {props.configPurchased.sort.browser.map((i, key) => {

                                return (<MenuItem key={key} value={i.title}>{i.icon}{i.title}</MenuItem>);

                            })}

                        </Select>
                    </FormControl>
                    <FormControl variant="filled" className='w118'>
                        <InputLabel id="demo-simple-select-filled-label"> <FormattedMessage  id='filterPanel.version' /></InputLabel>
                        <Select
                            labelId="demo-simple-select-filled-label"
                            value={props.configPurchased.currentSort.browserVersion}
                            onChange={(e)=>{browserVersionsEventHandler(e)}}>

                            {props.configPurchased.sort.browser.map((i) => {
                                if(i.title === props.configPurchased.currentSort.browser){
                                    return i.version.map((version, key) => {
                                        return (<MenuItem key={key} value={version}>{version}</MenuItem>);
                                    });
                                }
                            })}

                        </Select>
                    </FormControl>
                </div>
                <FormattedMessage  id='filterPanel.search'>
                    {(msg) => ( <TextField
                        variant="filled"
                        fullWidth
                        className="search_area"
                        placeholder={msg}
                        value={searchValue}
                        onChange={(e)=>{searchEventHandler(e)}}>
                    </TextField>)}
                </FormattedMessage>
                <div className="search_additional_components">
                    <div className="calendar">{IconPack.searchComponents.calendar}</div>

                </div>
            </div>
            <div>
                <Table className="shopTable configTable">
                    <TableHead>
                        <TableRow>{isCheckboxesActive ? tableCheckBox('all', null) : null }<TableCell className="w225"><FormattedMessage id='table.deviceOsBrowser' /></TableCell>
                            <TableCell className="padding-sides-10 w100"><FormattedMessage id='table.screen' /></TableCell>
                            <TableCell className="padding-sides-5"><FormattedMessage id='table.useragent' /></TableCell>
                            <TableCell className="padding-sides-10"><FormattedMessage id='table.videocard' /></TableCell>
                            <TableCell className="w150 padding-sides-25 text-align-right"><FormattedMessage id='table.created' /></TableCell>
                            <TableCell className="w125 padding-sides-0"><FormattedMessage id='table.buyerNickname' /></TableCell>
                            <TableCell className="w75 padding-sides-20"><FormattedMessage id='table.price' /></TableCell>
                            <TableCell className="w60"></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>{ isSearchActive ? tableSearchContent : tableContent }</TableBody>
                </Table>
            </div>
        </div>);
}

const mapStateToProps = (state) => ({
    configPurchased: state.configPurchased.configShop,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setDeviceFilter: (prop) => dispatch(setDeviceFilter(prop)),
        updateData: () => dispatch(updateData()),
        setOsFilter: (prop) => dispatch(setOsFilter(prop)),
        setOsVersionFilter: (prop) => dispatch(setOsVersionFilter(prop)),
        setBrowserFilter: (prop) => dispatch(setBrowserFilter(prop)),
        setBrowserVersionFilter: (prop) => dispatch(setBrowserVersionFilter(prop)),
        searchData: (prop) => dispatch(searchData(prop)),
        selectItem: (prop) => dispatch(selectItem(prop)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Purchased);

