import React from 'react';
import './style.scss'
import userAvatar from './avatar.svg'
import Switch from "@material-ui/core/Switch/Switch";
import {setAdditionalScreenCorporate} from "../../../redux/licenses/licenses.actions";

import {setChangeEmailModal,
    setChangePasswordlModal, setChangeTelegramModal} from "../../../redux/app/app.actions";

import {connect} from "react-redux";

const Profile = (props) => {

    let name = 'Lionel Messi';
    let registerDate = 'August 20, 2020';

    const [value1, setValue1] = React.useState();

    return (
        <div id="dashboard-profile">
            <header>
                <img src={userAvatar} alt=""/>
                <div>
                    <h2 className="font--exo2">{name}</h2>
                    <h5>Registered {registerDate}</h5>
                </div>
            </header>
            <main>
                <div className="profile-info">
                    <h5>Login</h5>
                    <p>{name}</p>
                    <div className="button button--disabled">CANNOT BE CHANGED</div>
                </div>
                <div className="profile-info">
                    <h5>Email</h5>
                    <p>{name}@gmail.com</p>
                    <div className="button"
                    onClick={()=> {props.setChangeEmailModal(true)}}
                    >CHANGE</div>
                </div>
                <div className="profile-info">
                    <h5>Telegram</h5>
                    <p>@{name}</p>
                    <div className="button" onClick={()=> {props.setChangeTelegramModal(true)}}>CHANGE</div>
                </div>
                <div className="profile-info">
                    <h5>Password</h5>
                    <p>******************</p>
                    <div className="button" onClick={()=> {props.setChangePasswordlModal(true)}}>CHANGE</div>
                </div>
                <div className="profile-info">
                    <h5>Two factor authorization</h5>
                    <div className="switch-wrapper">
                    <Switch checked={ value1 }
                            name="autoRenew"
                            onChange={(e)=>{ setValue1(!value1)}} />
                    <p className="inline "><span className=" color-white">Enabled</span></p>
                </div>
                    <div className="button">CHANGE</div>
                </div>



            </main>
        </div>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setChangeEmailModal: (bool) => dispatch(setChangeEmailModal(bool)),
        setChangePasswordlModal: (bool) => dispatch(setChangePasswordlModal(bool)),
        setChangeTelegramModal: (bool) => dispatch(setChangeTelegramModal(bool)),

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Profile);
