import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../../Images/IconPack";

import InputLabel from "@material-ui/core/InputLabel/InputLabel";

import TextField from "@material-ui/core/TextField/TextField";
import { FormattedMessage } from 'react-intl';

const TransactionsTable = (props) => {
    const [searchValue, setSearchValue] = React.useState('');
    const [dateReverse, setDateReverse] = React.useState(false);



    const getDayRow = (type) => {
        let date = new Date();
        var month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
            "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][date.getMonth()];
        let day;
        switch (type) {
            case 'Today':
                day = date.getDate();
                break;
            case 'Yesterday':
                day = date.getDate() - 1;
                break;
            default:
                break;
        }

        return (<TableRow className="date-row">
            <TableCell className="padding-sides-20 color-white" colspan="6">
                <b className="color-white">{type}</b>
                <span className="color-grey inline">{day} {month}</span>
            </TableCell>
        </TableRow>);
    }


    const getTbodyData = (reversed) => {

        if(!reversed){
            return (<>
                {getDayRow('Today')}

                {props.balanceData.map((i, key) => {
                    return itemHTML(i, key);
                })}

                {getDayRow('Yesterday')}

                {props.balanceData.map((i, key) => {
                    return itemHTML(i, key);
                })}

            </>);
        }else {
            return (<>

                {getDayRow('Yesterday')}

                {props.balanceData.map((i, key) => {
                    return itemHTML(i, key);
                }).reverse()}


                {getDayRow('Today')}

                {props.balanceData.map((i, key) => {
                    return itemHTML(i, key);
                }).reverse()}

            </>);
        }
    };




    const prepearAmount = (amount, type) => {
        switch (type) {
            case 'loss':
                return (
                    <span className="red">{amount}</span>)
                break;
            case 'profit':
                return (<span className="green">{amount}</span>)
                break;
            default:
                return amount
                break;
        }
    }

    const timeConverter = (UNIX_timestamp) => {
        var a = new Date(UNIX_timestamp * 1000);
        var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        var hour = a.getHours();
        var min = a.getMinutes();
        var sec = a.getSeconds();
        var time = date + ' ' + month + ' '  + ' ' + hour + ':' + min + ':' + sec ;
        return time;
    }

    const itemHTML = (i, key) => {
        return  (
          <TableRow key={key}>
             <TableCell className="padding-sides-20 green text-align-right">{i.amount}</TableCell>
             <TableCell className="padding-sides-25 ">
                 <span className="icon-inside color-white align-items-baseline">{IconPack.operations.statusGood} {i.status}</span>
             </TableCell>
             <TableCell className="padding-sides-20 color-white">Bitcoin</TableCell>
             <TableCell className="padding-sides-15 color-white ">Top up balance</TableCell>
             <TableCell className="padding-sides-15"><span className="icon-inside icon-inside--right color-white align-items-baseline ">154895327595{IconPack.operations.id}</span></TableCell>
             <TableCell className="padding-sides-20 ">{timeConverter(i.date)}</TableCell>
         </TableRow>

        );
    }



    let searchedData = props.balanceData.filter(i => {

        if (
            i.status.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.paymentSystem.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.description.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.transactionID.toLowerCase().includes(searchValue.toLowerCase())
        ) {
            return i
        }

    })

    searchedData = searchedData.map((i, key) => {

        return itemHTML(i, key);
    });


    /* active */
    return (
        <div className="сustomStyle">
            <div className="filterPanel">

                    <FormattedMessage  id='filterPanel.search'>
                        {(msg) => ( <TextField
                            variant="filled"
                            fullWidth
                            className="search_area"
                            placeholder={msg}
                            value={searchValue}
                            onChange={(e)=>{setSearchValue(e.target.value)}}>
                        </TextField>)}
                    </FormattedMessage>

                <div className="search_additional_components">
                    <div className="calendar">{IconPack.searchComponents.calendar}</div>

                </div>
            </div>
            <Table className="configTable">

                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-20 w100 text-align-right"><FormattedMessage id='table.usd'/></TableCell>
                        <TableCell className="padding-sides-25 w175"><FormattedMessage id='table.status'/></TableCell>
                        <TableCell className="padding-sides-20 w150"><FormattedMessage id='table.paymentSystem'/></TableCell>
                        <TableCell className="padding-sides-15  "><FormattedMessage id='table.description'/></TableCell>
                        <TableCell className="padding-sides-15 w150 "><FormattedMessage id='table.transactionID'/></TableCell>
                        <TableCell className={dateReverse ? 'padding-sides-15 w125 td-date' : 'padding-sides-15 w125 td-date rotate-child-180 '} onClick={()=>{setDateReverse(!dateReverse)}}>{IconPack.tableIcons.date} <FormattedMessage id='table.date'/></TableCell>

                    </TableRow>
                </TableHead>

                <TableBody  className="no-hover">
                    {searchValue.length ? searchedData : getTbodyData(dateReverse) }
                </TableBody>
            </Table>
        </div>
    );
}

const mapStateToProps = (state) => ({
    balanceData: state.balance.balanceData.transactions,
});



export default connect(mapStateToProps)(TransactionsTable);






