import React, {Fragment} from 'react';
import {connect} from "react-redux";
import TabsManager from './../TabsManager/TabsManager';
import LicenseDashboard from './Dashboard/Dashboard';
import {setCurrentLicense} from '../../redux/licenses/licenses.actions'
import { FormattedMessage } from 'react-intl';


const Licenses = (props) => {

    let tabs;

    switch(props.licenses.currentLicense) {
        case 'Personal':
            tabs = [
                {name : (<FormattedMessage id='tabsManager.menu.activity'/>), link: '/licenses/' + props.currentUser.id + '/activity'},
                {name : (<FormattedMessage id='tabsManager.menu.costs'/>), link: '/licenses/' + props.currentUser.id + '/costs'},
            ];
            break;
        case 'Corporate':
            tabs = [
                {name : (<FormattedMessage id='tabsManager.menu.activity'/>), link: '/licenses/' + props.currentUser.id + '/activity'},
                {name : (<FormattedMessage id='tabsManager.menu.users'/>), link: '/licenses/' + props.currentUser.id + '/users'},
                {name : (<FormattedMessage id='tabsManager.menu.costs'/>), link: '/licenses/' + props.currentUser.id + '/costs'},
            ];
            break;
    }

    return (
        <Fragment>
            <TabsManager data={tabs} license={props.licenses} user={props.currentUser} licenseComponent={true}/>

            <LicenseDashboard tab={props.activeTab}/>

        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    licenses: state.licenses.licenses,
    currentUser: state.user.currentUser,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setCurrentLicense: (license) => dispatch(setCurrentLicense(license))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Licenses);
